@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{
    background: rgb(48,162,226);
    background: linear-gradient(90deg, rgba(48,162,226,1) 0%, rgba(0,255,170,1) 81%);
    box-sizing: content-box;
}
.backcard{
    background: rgb(48,162,226);
    background: linear-gradient(90deg,  rgba(0,255,170,1) 0%, rgba(48,162,226,1) 81%);
    box-sizing: content-box;
}
.fullcard{
    text-align: center;
}

.Heading{
    font-family:'Monoton';
    font-size: 2cm;
    font-weight: 100;
    font-style: italic;
}

::-webkit-input-placeholder{
    font-size: 2fr;
    font-style: italic;
}

:-ms-input-placeholder{
    font-size: 2fr;
    font-style: italic;
}

::placeholder{
    font-size: 2fr;
    font-style: italic;
}
input{
    width: 250px;
}

@media only screen and (max-width: 750px) {
    .Heading{
        font-size: 1cm;
    }
    .searching{
        padding: 1px;
    }
  }
.App {
  text-align: center;
}

